<template>
  <LoadingEl :active="isLoading" />
  <header class="container pt-3">
    <div class="text-center header px-4 py-5">
      <div class="chevron"></div>
      <div class="chevron"></div>
      <div class="chevron"></div>
      <div class="text-white text-center p-3 cover d-inline-block">
        <p class="mb-0"></p>
        <h1 class="fw-bold my-3">!</h1>
        <p class="mb-0"></p>
      </div>
    </div>
  </header>

  <section class="container py-5">
    <h2 class="text-center lh-lg">  </h2>
    <div class="row row-cols-1 row-cols-md-3">
      <div class="col">
        <div
          class="card border-0 animated"
          :class="{ fadeIn: animation.special }"
        >
          <div class="card-body text-center">
            <h5 class="card-title fs-1"><i class="bi bi-globe"/></h5>
            <h5 class="card-title fw-bold"></h5>
            <p class="card-text">
              !
            </p>
          </div>
        </div>
      </div>
      <div class="col">
        <div
          class="card border-0 animated"
          :class="{ fadeIn: animation.special }"
          style="transition: all 1.5s 0.3s"
        >
          <div class="card-body text-center">
            <h5 class="card-title fs-1"><i class="bi bi-truck"/></h5>
            <h5 class="card-title fw-bold"></h5>
            <p class="card-text">
              !
            </p>
          </div>
        </div>
      </div>
      <div class="col">
        <div
          class="card border-0 animated"
          :class="{ fadeIn: animation.special }"
          style="transition: all 1.5s 0.6s"
        >
          <div class="card-body text-center">
            <h5 class="card-title fs-1"><i class="bi bi-people"/></h5>
            <h5 class="card-title fw-bold"></h5>
            <p class="card-text">
              <a
                href="mailto:toy-factory@support.com"
                class="text-decoration-none"
                ></a
              >!
            </p>
          </div>
        </div>
      </div>
    </div>
  </section>

  <section class="container py-5">
    <div
      class="row flex-row-reverse g-3 animated"
      :class="{ fadeIn: animation.first }"
    >
      <div class="col-lg-6 d-flex align-items-center">
        <div>
          <h2 class="fw-bold"><br /></h2>
          <p class="lh-lg text-secondary mb-0">
            ?<br />!~
          </p>
          <p class="text-end text-secondary"></p>
          <button
            type="button"
            class="btn btnStyle"
            @click.prevent="$router.push('/products')"
          >
            ~
          </button>
        </div>
      </div>
      <div class="col-lg-6">
        <img src="../assets/images/legoman.jpg" alt="" class="w-100" />
      </div>
    </div>
  </section>

  <section class="py-3">
    <div class="container">
      <div class="d-flex justify-content-between">
        <h3><i class="bi bi-star-fill text-warning"/> </h3>
        <a
          href="#"
          class="text-decoration-none fs-5 text-end fw-bold"
          @click.prevent="$router.push('/products')"
          ><i class="bi bi-arrow-right"></i
        ></a>
      </div>
      <swiper
        :slidesPerView="4"
        :spaceBetween="30"
        :autoplay="{
          delay: 3000,
          disableOnInteraction: false,
        }"
        :loop="true"
        :modules="modules"
        :breakpoints="{
          '0': {
            slidesPerView: 1,
            spaceBetween: 20,
          },
          '576': {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          '768': {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          '992': {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          '1200': {
            slidesPerView: 4,
            spaceBetween: 30,
          },
        }"
        class="mySwiper"
      >
        <swiper-slide
          v-for="item in randomProducts"
          :key="item.id"
          class="h-auto"
        >
          <div class="card h-100 border">
            <img
              :src="item.imageUrl"
              class="card-img-top img-fluid"
              :alt="item.title"
              style="object-fit: cover; height: 200px; cursor: pointer"
            />
            <div class="card-body d-flex flex-column justify-content-between">
              <div>
                <div class="d-flex justify-content-between align-items-center">
                  <h5 class="card-title fw-bold text-primary mb-0">
                    {{ item.title }}
                  </h5>
                  <span class="badge bg-warning rounded-pill text-black">
                    {{ item.category }}</span
                  >
                </div>
                <hr />
                <p class="card-text">{{ item.description }}</p>
              </div>
              <div>
                <div class="text-end">
                  <p class="h5" v-if="item.price === item.origin_price">
                    {{ $filters.currency(item.origin_price) }} 
                  </p>
                  <del
                    class="h6 text-danger"
                    v-if="item.price !== item.origin_price"
                    > {{ $filters.currency(item.origin_price) }} </del
                  >
                  <p class="h5" v-if="item.price !== item.origin_price">
                     {{ $filters.currency(item.price) }} 
                  </p>
                </div>
                <div class="text-end position-relative" style="z-index:3;">
                  <button
                    type="button"
                    class="btn btn-primary btnCircle mx-2 rounded-circle fs-4 text-white"
                    :disabled="status.loadingItem === item.id"
                    @click.prevent="addCart(item, item.id)"
                  >
                    <i class="bi bi-cart3"/>
                  </button>
                </div>
              </div>
            </div>
            <a href="#" class="stretched-link" @click.prevent="getProduct(item.id)"></a>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </section>

  <section class="container py-5">
    <div
      class="coupon d-flex align-items-center justify-content-end overflow-hidden"
    >
      <div
        class="text-center p-lg-5 p-3 animatedRight"
        :class="{ fadeInX: animation.second }"
      >
        <h2 class="fs-1 fw-bold">
          <i class="bi bi-balloon-fill text-danger"/><i
            class="bi bi-balloon-fill text-danger"
          />
        </h2>
        <p class="fs-3">
          !<br class="d-lg-none" />
          "<span class="text-danger fw-bold">toy777</span>"<br
            class="d-lg-none"
          />7!
        </p>
        <button type="button" class="btn btnStyle" @click="copy">
          
        </button>
      </div>
    </div>
  </section>

  <section class="container pb-5 py-3">
    <div
      class="subscribe d-flex align-items-center p-5 row-cols-1 row-cols-md-2"
    >
      <div
        class="col p-md-4 animatedLeft"
        :class="{ fadeInX: animation.third }"
      >
        <h3><br />!</h3>
        <div class="input-group mb-3">
          <input
            type="text"
            class="form-control lh-lg"
            placeholder="Email"
          />
          <button class="btn btn-dark" type="button"></button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Autoplay } from 'swiper'

// Import Swiper styles
import 'swiper/css'

import 'swiper/css/pagination'

export default {
  data () {
    return {
      products: [],
      randomProducts: [],
      isLoading: false,
      status: {
        loadingItem: ''
      },
      animation: {
        special: false,
        first: false,
        second: false,
        third: false
      }
    }
  },
  inject: ['emitter'],
  components: {
    Swiper,
    SwiperSlide
  },
  setup () {
    return {
      modules: [Autoplay]
    }
  },
  methods: {
    getProducts () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      this.isLoading = true
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.products = res.data.products
          this.getRandomProducts()
        }
        this.isLoading = false
      })
    },
    getProduct (id) {
      this.$router.push(`/products/${id}`)
    },
    copy () {
      navigator.clipboard.writeText('toy777')
      alert('!')
    },
    addCart (item, id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.isLoading = true
      this.status.loadingItem = id
      const cart = {
        product_id: id,
        qty: 1
      }
      this.$http.post(url, { data: cart }).then((res) => {
        this.status.loadingItem = ''
        this.emitter.emit('update-cart', id)
        this.isLoading = false
        this.$httpMessageState(res, `"${item.title}"`)
      })
    },
    getRandomProducts () {
      this.randomProducts = this.products.sort(() => Math.random() - 0.5)
      this.randomProducts.splice(0, parseInt(this.randomProducts.length) - 6)
    },
    handleScroll () {
      this.animation.special = window.scrollY > 100
      this.animation.first = window.scrollY > 400
      this.animation.second = window.scrollY > 1400
      this.animation.third = window.scrollY > 1700
    }
  },
  created () {
    this.getProducts()
    window.addEventListener('scroll', this.handleScroll)
  }
}
</script>
